@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
  box-sizing: border-box;
  background: transparent;
  font-family: inherit;
  }
  a{
  text-decoration: none;
  }
  a img {
  border: none;
  }
  
  button,
  a {
  cursor: pointer;
  color: inherit;
  }
  
  textarea {
  resize: none;
  }
  
  button,
  textarea,
  select {
  -webkit-appearance: none !important;
          appearance: none !important;
  border-radius: 0;
  }
  
  /* --------end of css reset--------- */

body {
  width: 100vw;
  height: 100vh;
  font-family: 'Space Grotesk', sans-serif;
  background-color: #ffeedf;
  color: #145434;
  
}

#root {
  width: 100vw;
  height: 100vh;
}

.app {
  width: 100%;
  margin: 0 auto;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 100%;
  height: 60px;
  margin: 0;
  border-bottom: 1px solid #145434;
  font-size: 36px;
  font-weight: 600;
}



.card-container {
  width: 1000px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 60px;
  gap: 60px;
  margin-top: 60px;
  margin-left: auto;
  margin-right: auto;
}

.card {
  width: 410px;
  min-width: 380px;
  min-height: 250px;
  box-sizing: border-box;
  background-color: #145434;
  color: #ffeedf;
  padding-left: 40px;
  padding-right: 40px;;
  position: relative;
}

button {
  position: relative;
  position: absolute;
  top: 210px;
  right: -6px;
  background-color: #ffeedf;
  color: #145434;
  width: 200px;
  height: 50px;
  border: 2px solid #145434;
  font-size: 14px;
}

button:hover{
  background-color:#145434 ;
  color: #ffeedf;
  border: 2px solid #ffeedf;
}

#arrow {
  position: absolute;
  right: 0;
  top: 6px;
  padding-bottom: 20px;
  font-size: 36px;
}
.number {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #145434;
  background-color: #ffeedf;
  color: #145434;
  -webkit-transform: translate(-147%, -50%);
          transform: translate(-147%, -50%);
  text-align: center;
}

.number p {
  font-size: 24px;
  margin-top: 2px;
}

.text {
  display: flex;
  flex-direction: column;
  grid-gap: 40px;
  gap: 40px;
  width: 100%;
  height: 100px;
}

.text h2 {
  font-weight: 400;
  font-size: 36px;
}
.text h3 {
  font-weight: 400;
  font-size: 18px;
  margin-top: 10px;
}
.text h4 {
  font-weight: 600;
  font-size: 18px;
  margin: 0;
}

.subtext {
  display: flex;
  flex-direction: column;
  height: 70px;
  width: 100%;
  justify-content: center;
  border-top: 2px solid;

}
@media screen and (max-width: 900px) {
  .card-container {
    width: 500px;
   }
}


@media screen and (max-width: 420px) {
  .card-container {
   width: 420px;
   padding-right: 40px;
   padding-left: 40px;
  }

  .card {
    width: 100%;
    max-width: 380px;
    min-width: none;
  }
}





